.error-bar {
  border: 4px solid #EF5350;
  background: #FFEBEE;
  font-size: 18px;
  margin: 20px 0 0;
  position: relative;
}
.error-bar__content {
  padding: 15px 50px 15px 16px;
  margin: 0;
  position: relative;
  z-index: 1;
}
.error-bar__reset {
  position: absolute;
  top: 13px;
  right: 13px;
  cursor: pointer;
  width: 28px;
  height: 28px;
  z-index: 1;
  background: transparent;
  padding: 0;
  border: none;
}
.error-bar__reset:focus {
  outline: none;
}
.error-bar__reset-inner {
  position: relative;
  display: inline-block;
  vertical-align: top;
  width: 28px;
  height: 28px;
  border: 2px solid transparent;
  border-radius: 50%;
  -webkit-transition: all .2s ease;
  -o-transition: all .2s ease;
  transition: all .2s ease;
}
.error-bar__reset-inner:before,
.error-bar__reset-inner:after {
  content: "";
  position: absolute;
  z-index: 1;
  height: 4px;
  background: #EF5350;
  left: 3px;
  right: 3px;
  top: 50%;
  margin-top: -2px;
}
.error-bar__reset-inner:before {
  -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
          transform: rotate(45deg);
}
.error-bar__reset-inner:after {
  -webkit-transform: rotate(-45deg);
      -ms-transform: rotate(-45deg);
          transform: rotate(-45deg);
}
.error-bar__reset:focus .error-bar__reset-inner,
.error-bar__reset:hover .error-bar__reset-inner {
  border-color: #EF5350;
  background: #fff;
}
.error-bar__item {
  list-style-position: inside;
}
.error-bar__item:only-child {
  list-style: none;
}
@media screen and (min-width: 800px) {
  .error-bar__content {
    padding-left: 36px;
  }
}



